// CustomSelect.js
import React, { useState } from "react";
import "./CustomSelect.css"; // To file CSS  ty chnh

const CustomSelect = ({ options, placeholder }) => {
  // State qun l gi tr c chn v trng thi hin th danh sch
  const [selectedOption, setSelectedOption] = useState(null);
  const [isOpen, setIsOpen] = useState(false);

  // Hm x l khi chn mt mc
  const handleOptionClick = (option) => {
    setSelectedOption(option);
    setIsOpen(false); // ng danh sch sau khi chn
  };

  return (
    <div className="custom-select-container">
      {/* Hin th gi tr  chn hoc placeholder */}
      <div className="custom-select-header" onClick={() => setIsOpen(!isOpen)}>
        {selectedOption ? selectedOption.label : placeholder}
        <span className={`arrow ${isOpen ? "open" : ""}`}>
          <i className="bi bi-chevron-down"></i>
        </span>
      </div>

      {/* Hin th danh sch ty chn */}
      {isOpen && (
        <div className="custom-select-options">
          {options.map((option) => (
            <div
              key={option.value}
              className="custom-select-option"
              onClick={() => handleOptionClick(option)}
            >
              {option.label}
            </div>
          ))}
        </div>
      )}
    </div>
  );
};

export default CustomSelect;
